    font-size: 0.8rem;
}
.navbar-other .nav-item .cart-dropdown-menu .meta.price,
.navbar-other .nav-item .cart-dropdown-menu .meta.price span {
    color: #aaa !important;
}
.navbar-other .nav-item .cart-dropdown-menu .meta.price .quantity {
