    padding-right: 5px;
}
.badge-cart {
    text-transform: uppercase;
    color: var(--color-white);
    font-size: 0.55rem;
    font-weight: 500;
    line-height: 0.85rem;
    width: 0.85rem;
    height: 0.85rem;
    border-radius: 100%;
    text-align: center;
