/*-----------------------------------------------------------------------------------*/
/*	10. BUTTON
/*-----------------------------------------------------------------------------------*/
.btn {
    color: var(--color-white);
    background: var(--color-default);
    border: 0;
    margin-bottom: 0.5rem;
    margin-right: 0.25rem;
    padding: 0.9rem 1.3rem 0.8rem;
    font-size: 0.65rem;
    letter-spacing: 0.025rem;
    text-transform: uppercase;
    font-weight: 500;
    line-height: 1;
    border-radius: 1.5rem;
    text-shadow: none;
    transition: all 300ms ease-in-out;
    box-shadow: none;
    display: inline-block;
}
.btn-icon i {
    vertical-align: -1px;
}
.navigation .btn-icon i {
    vertical-align: 0;
}
.btn-icon-left i {
    margin-right: 0.25rem;
}
.btn-icon-right i {
    margin-left: 0.25rem;
}
.btn:focus {
    color: var(--color-white);
    box-shadow: inherit;
}
.btn:hover,
.btn:active,
.btn.active {
    color: var(--color-white);
    box-shadow: var(--btn-hover-default);
}
.btn.btn-white {
    color: #404040;
}
.text-center .btn {
    margin-left: 0.25rem;
    margin-right: 0.25rem;
}
.text-right .btn {
    margin-right: 0;
}
.btn-s {
    padding: 0.65rem 0.9rem 0.55rem;
}
