/*-----------------------------------------------------------------------------------*/
/* 	13. IMAGE BACKGROUND WRAPPER
/*-----------------------------------------------------------------------------------*/
.image-wrapper {
    background: no-repeat center center;
    background-size: cover;
    position: relative;
}
.image-wrapper:not(.mobile) {
    background-attachment: fixed !important
}
.image-wrapper.bg-auto {
    background-size: auto;
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: scroll !important;
}
.image-wrapper.bg-full {
    background-size: 100%;
    background-repeat: no-repeat;
    background-attachment: scroll !important;
}
.image-wrapper:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: rgba(var(--rgb-dark), 0.5);
}
.image-wrapper.rounded:before {
    border-radius: 0.2rem !important;
}
.image-wrapper.gradient-overlay:before {
    background: none;
    background-image: linear-gradient(135deg, rgba(111, 63, 124, 0.55) 0%, rgba(88, 73, 170, 0.55) 100%);
}
.image-wrapper.no-overlay:before {
    display: none;
}
.image-wrapper:not(.no-overlay) * {
    position: relative;
    z-index: 2;
}
.image-wrapper .cbp-nav-controls,
.image-wrapper .tparrows {
    position: absolute;
}
.h-100vh {
    height: 100vh;
}
/*-----------------------------------------------------------------------------------*/
/*	14. IMAGE BLOCK WRAPPER
/*-----------------------------------------------------------------------------------*/
.image-block-wrapper {
    padding: 0px;
    position: relative;
}
.image-block-wrapper,
.image-block-wrapper .box {
    min-height: 450px;
}
.image-block-wrapper .image-block {
    overflow: hidden;
    position: absolute;
    height: 100%;
    top: 0px;
}
.image-block-wrapper .image-block .image-block-bg {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-size: cover !important;
    background-position: center center !important;
}
.image-block-wrapper [class*="container"] .box {
    padding: 6rem 5rem;
}
/*-----------------------------------------------------------------------------------*/
/*	15. VIDEO BACKGROUND WRAPPER
/*-----------------------------------------------------------------------------------*/
.video-wrapper video {
    width: 100%;
    height: 100%;
    display: inline-block;
    vertical-align: baseline;
    background-size: cover;
}
.video-wrapper-inner {
    height: 30rem;
    width: 100%;
}
.video-wrapper .content-overlay {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 20;
    width: 100%;
    height: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: rgba(var(--rgb-dark), 0.5);
}
video::-webkit-media-controls {
    display: none !important
}
/*-----------------------------------------------------------------------------------*/
/*	16. SHADOW
/*-----------------------------------------------------------------------------------*/
.shadow {
    box-shadow: var(--shadow) !important;
    transition: all 200ms ease-in-out;
}
.shadow-hover:hover {
    box-shadow: var(--shadow-hover) !important;
}
.no-shadow {
    box-shadow: none !important;
}
/*-----------------------------------------------------------------------------------*/
/*	17. ROUNDED CORNERS
/*-----------------------------------------------------------------------------------*/
.rounded {
    border-radius: 0 !important;
}
.rounded:not(.main),
figure.rounded:not(.main) span.bg,
figure.rounded:not(.main) img,
.rounded:not(.main).overlay figcaption,
.owl-carousel figure.rounded:not(.main) img,
.owl-carousel .item>img,
.google-map.rounded iframe {
    border-radius: 0.2rem !important;
}
figure.main.rounded span.bg,
figure.main.rounded img,
.main.rounded.overlay figcaption {
    border-top-left-radius: 0.2rem !important;
    border-top-right-radius: 0.2rem !important;
}
/*-----------------------------------------------------------------------------------*/
/*	18. CIRCLE
/*-----------------------------------------------------------------------------------*/
.full-circle {
    display: inline-block;
    border-radius: 50%;
    min-width: 5rem;
    min-height: 5rem;
    padding: 1.2rem;
    text-align: center;
    box-sizing: content-box;
    white-space: nowrap;
}
.full-circle:before {
    content: "";
    display: inline-block;
    vertical-align: middle;
    padding-top: 100%;
    height: 0;
}
.full-circle .full-circle-inner {
    display: inline-block;
    vertical-align: middle;
}
/*-----------------------------------------------------------------------------------*/
/*	19. DIVIVDER
/*-----------------------------------------------------------------------------------*/
hr {
    border: 0;
    padding: 0;
    margin: 0;
    border-bottom: 1px solid rgba(var(--rgb-black), 0.1);
    padding-top: 3.5rem;
    margin-bottom: 3.5rem;
}
hr.hr-footer {
    padding-top: 0;
    margin-bottom: 0;
}
hr.dashed {
    border-bottom-width: 2px;
    border-style: dashed;
}
hr.dotted {
    border-bottom-width: 2px;
    border-style: dotted;
}
hr.double {
    border-bottom-width: 4px;
    border-style: double;
}
_:default:not(:root:root),
hr.double {
    border-bottom-width: 3px;
}
.divider-icon {
    border: 0;
    position: relative;
    padding-top: 3.5rem;
    padding-bottom: 3.5rem;
}
.divider-icon i {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 1.5rem;
    color: rgba(0, 0, 0, 0.1);
}
.divider-icon:before,
.divider-icon:after {
    position: absolute;
    top: 50%;
    content: "";
    border-top: 1px solid rgba(var(--rgb-black), 0.1);
    width: calc(50% - 1.5rem)
}
.divider-icon:before {
    left: 0;
}
.divider-icon:after {
    right: 0;
}
.divider-heading {
    border: 0;
    position: relative;
    padding: 0;
    margin-bottom: 1rem;
    line-height: 1;
    color: var(--color-default);
    opacity: 0.25;
    text-align: center;
}
.divider-heading i {
    font-size: 1.4rem;
    line-height: 1;
}
