/*-----------------------------------------------------------------------------------*/
/* 	22. ALERT
/*-----------------------------------------------------------------------------------*/
.alert {
    border: 0;
    padding: 1rem 1.25rem;
}
.alert-dismissible {
    padding-right: 4rem;
}
.alert-link {
    font-weight: 500;
}
button.close {
    transition: all 150ms ease-in-out;
    cursor: pointer;
    text-shadow: none;
    font-weight: normal;
}
button.close span {
    display: none;
}
button.close:before {
    font-family: 'Jam';
    content: "\ea05";
    vertical-align: middle;
    display: table-cell;
    font-size: 1.2rem;
    vertical-align: 1px;
}
.alert-dismissible button.close {
    padding: 0 1rem 0 0;
    top: 50%;
    transform: translateY(-50%);
}
/*-----------------------------------------------------------------------------------*/
/* 	23. MODAL
/*-----------------------------------------------------------------------------------*/
.modal {
    background: rgba(var(--rgb-dark), 0.4);
}
.modal-content {
    border: 0;
    border-radius: 0.2rem;
}
.modal button.close {
    position: absolute;
    z-index: 1;
    right: 10px !important;
    top: 10px !important;
    padding: 0;
    color: rgba(var(--rgb-black), 0.7);
}
.modal .close,
.modal .close:not(:disabled):not(.disabled):focus,
.modal .close:not(:disabled):not(.disabled):hover {
    opacity: 1;
}
.modal .close:not(:disabled):not(.disabled):focus,
.modal .close:not(:disabled):not(.disabled):hover {
    color: rgba(var(--rgb-black), 0.5);
}
.modal button.close.close-inverse {
    color: rgba(var(--rgb-white), 1);
}
.modal button.close.close-inverse:not(:disabled):not(.disabled):focus,
.modal button.close.close-inverse:not(:disabled):not(.disabled):hover {
    color: rgba(var(--rgb-white), 0.8);
}
/*-----------------------------------------------------------------------------------*/
/* 	24. BLOCKQUOTE
/*-----------------------------------------------------------------------------------*/
blockquote p {
    font-size: 0.9rem;
    line-height: 1.5rem;
    font-style: normal;
    color: #707070;
}
blockquote.icon p {
    font-size: 1rem;
    line-height: 1.6rem;
    font-style: normal;
}
blockquote.larger p {
    font-size: 1.1rem;
    line-height: 1.7rem;
    font-weight: 300;
}
blockquote.icon {
    position: relative;
    text-align: center;
    padding-top: 2.5rem;
}
.owl-carousel blockquote.icon {
    padding-top: 3rem;
}
blockquote.icon:before {
    font-family: sans-serif;
    content: "\201D";
    color: rgba(var(--rgb-default), 0.3);
    z-index: 1;
    position: absolute;
    width: 2.5rem;
    font-size: 6rem;
    line-height: 4.5rem;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    vertical-align: bottom;
}
blockquote.icon.icon-left:before {
    left: 0;
    transform: none;
    content: "\201C";
}
.blockquote-footer {
    font-size: 0.6rem;
    line-height: 1.05rem;
    font-weight: 500;
    text-transform: uppercase;
    padding: 0;
    margin: 0;
    color: #999;
}
.blockquote-footer:before {
    vertical-align: 1px;
}
.blockquote-footer.details {
    text-transform: none;
}
.blockquote-footer.details span {
    text-transform: uppercase;
    margin-bottom: 1rem;
    display: block;
}
.blockquote-footer.details:before {
    display: none;
}
.blockquote-details {
    display: flex;
    align-items: center;
    text-align: left;
    margin-bottom: 1rem;
}
.blockquote-details .info {
    padding-left: 1rem;
}
.blockquote-details .img-blob img {
    width: 3.5rem;
}
.blockquote-details span {
    font-size: 0.8rem;
    font-weight: 400;
    text-transform: none;
}
/*-----------------------------------------------------------------------------------*/
/*	25. LISTS
/*-----------------------------------------------------------------------------------*/
ul,
ol {
    padding-left: 15px;
}
.unordered-list {
    list-style: none;
    padding: 0;
}
.unordered-list>li:before {
    font-family: sans-serif;
    font-size: 1.1rem;
    vertical-align: middle;
    display: inline-block;
    margin-right: 0.35rem;
    content: "\2022";
}
.mega-menu-content .unordered-list>li:before {
    vertical-align: 0;
}
.unordered-list-circle {
    list-style-type: circle;
}
.unordered-list-disc {
    list-style-type: disc;
}
.ordered-list-roman {
    list-style-type: upper-roman;
}
.ordered-list-alpha {
    list-style-type: lower-alpha;
}
.list-inline {
    margin-left: -0.5rem
}
.list-inline>li {
    padding-left: 0.5rem;
    padding-right: 0.5rem
}
.list-inline>li.flex-middle {
    display: flex !important;
}
.icon-list {
    list-style: none;
    padding: 0;
}
.icon-list i {
    padding-right: 0.5rem;
    font-size: 0.75rem;
    vertical-align: -1px;
}
.image-list {
    padding: 0;
    margin: 0;
}
.image-list p,
.image-list .meta {
    margin: 0;
}
.image-list:after {
    content: '';
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}
.image-list li {
    clear: both;
    margin-bottom: 1rem;
    display: block;
    overflow: hidden;
}
.image-list li:last-child {
    margin: 0;
}
.image-list li a:hover {
    color: var(--color-default)
}
.image-list figure {
    float: left;
    width: 70px;
    height: 70px;
}
.image-list .post-content {
    margin-left: 85px;
    margin-bottom: 0;
}
.tag-list li {
    display: inline-block;
}
.category-list .badge {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    margin: 0 0 0 0.5rem;
}
/*-----------------------------------------------------------------------------------*/
/*	26. SERVICES
/*-----------------------------------------------------------------------------------*/
