/*-----------------------------------------------------------------------------------*/
/* 	27. CLIENTS
/*-----------------------------------------------------------------------------------*/
.clients img {
    opacity: 0.8;
    transition: all 200ms ease-in-out;
}
.clients img:hover {
    opacity: 1;
}
/*-----------------------------------------------------------------------------------*/
/*	28. FIGURE
/*-----------------------------------------------------------------------------------*/
figure {
    margin: 0;
    padding: 0;
}
figure img {
    max-width: 100%
}
figure.cover img {
    width: 100%;
}
figure.cover {
    position: relative;
}
figure.cover .divider [class*="fill-"] {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;
    width: 100%;
}
/*-----------------------------------------------------------------------------------*/
/*	29. OVERLAY
/*-----------------------------------------------------------------------------------*/
.overlay {
    position: relative;
    text-align: center;
    overflow: hidden;
    transform: translate3d(0px, 0px, 0px);
}
figure.overlay img {
    opacity: 1;
    width: 100%;
    max-width: 100%;
    vertical-align: top;
    position: relative;
}
.classic-view figure {
    width: auto;
    height: auto;
}
.overlay figcaption,
.overlay .figcaption {
    padding: 0.5rem;
    z-index: 5;
    pointer-events: none;
}
.overlay * {
    transition: all 0.4s ease;
}
.overlay img {
    transition: none;
}
.overlay:not(.caption-overlay) i {
    display: block;
    font-size: 1.4rem;
}
.overlay a,
.overlay span.bg {
    position: relative;
    display: block;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 3;
    cursor: pointer !important;
}
.overlay span.bg {
    opacity: 0;
    z-index: 4;
    position: absolute;
    width: 100%;
    height: 100%;
}
.overlay span.bg,
.overlay5 figcaption {
    background: rgba(var(--rgb-dark), 0.6);
}
.overlay.color span.bg,
.overlay5.color figcaption {
    background: rgba(var(--rgb-default), 0.9);
}
.overlay.gradient span.bg,
.overlay5.gradient figcaption {
    background: none;
    background-image: linear-gradient(125deg, rgba(95, 134, 190, 0.85) 0%, rgba(167, 114, 185, 0.85) 100%);
}
.overlay:hover span.bg {
    opacity: 1;
    transition: opacity 0.35s ease-in-out;
}
.overlay * {
    color: var(--color-white) !important;
}
.overlay img {
    max-width: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    vertical-align: top;
}
.overlay1 * {
    box-sizing: border-box;
}
.overlay1 figcaption {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 0.75rem 1rem;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    opacity: 0;
    text-align: center;
}
.overlay1 .from-top {
    position: absolute;
    padding: 0.75rem 1rem;
    top: 50%;
    left: 0;
    width: 100%;
    transform: translateY(-80%);
}
.overlay1:hover figcaption,
.overlay1:hover .figcaption {
    opacity: 1;
}
.overlay1:hover figcaption *,
.overlay1:hover .figcaption * {
    transform: translateY(-50%);
}
.overlay2:before,
.overlay2:after {
    content: "";
    background-color: var(--color-white);
    position: absolute;
    z-index: 5;
    top: 50%;
    left: 50%;
    opacity: 0;
    transition: all 0.4s ease;
    transform: translate(-50%, -50%);
}
.overlay2.light:before,
.overlay2.light:after {
    background-color: #404040;
}
.overlay2:before {
    width: 30px;
    height: 2px;
}
.overlay2:after {
    height: 30px;
    width: 2px;
}
.overlay2:hover:before,
.overlay2:hover:after {
    opacity: 1;
    transition-delay: 0.2s;
    top: 50%;
    left: 50%;
}
.overlay3 * {
    position: absolute;
    left: 0;
    margin: 0;
    width: 100%;
}
.overlay3>* {
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    height: 100%;
    position: absolute;
}
.overlay3 figcaption {
    height: 100%;
    opacity: 0;
}
.overlay3 img {
    position: relative;
}
.overlay3 .from-top {
    bottom: 50%;
    padding: 0 1rem;
    transform: translate3d(0%, -100%, 0);
}
.overlay3 .from-bottom {
    top: 50%;
    padding: 0 1rem;
    transform: translate3d(0%, 100%, 0);
}
.overlay3:hover * {
    opacity: 1;
}
.overlay3:hover figcaption * {
    transform: translate3d(0%, 0%, 0);
}
.hover-scale img {
    transition: all 0.35s ease-in-out;
}
.hover-scale:hover img {
    transform: scale(1.1);
}
.overlay4 figcaption {
    position: absolute;
    left: 0;
    bottom: 0;
    top: auto;
    right: auto;
    opacity: 0;
    padding: 30px;
    text-align: left;
}
.overlay4:hover figcaption {
    opacity: 1;
}
.overlay5 figcaption {
    position: absolute;
    width: 100%;
    text-align: center;
    padding: 15px;
}
.overlay5:hover figcaption {
    transform: translateY(-100%);
}
.overlay5 span.bg {
    display: none;
}
.overlay6 figcaption,
.overlay6 .figcaption {
    position: absolute;
    transition: all 0.35s ease;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
}
.overlay6:hover figcaption,
.overlay6:hover .figcaption {
    opacity: 1;
}
.overlay.caption figcaption,
.overlay.caption .figcaption {
    position: absolute;
    transition: all 0.4s ease;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
}
.overlay.caption:hover figcaption,
.overlay.caption:hover .figcaption {
    opacity: 1;
    transition-delay: 0.2s;
}
.overlay.caption figcaption,
.overlay.caption .figcaption {
    opacity: 1;
}
.overlay.caption.caption-overlay span.bg {
    opacity: 1;
    background: none;
    background: rgba(var(--rgb-dark), 0) linear-gradient(transparent 15%, transparent 20%, rgba(var(--rgb-dark), 0.3) 55%, rgba(var(--rgb-dark), 0.7) 85%, rgba(var(--rgb-dark), 0.75) 100%) repeat scroll 0 0;
}
.overlay.caption.caption-overlay span.bg:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
    background: rgba(var(--rgb-dark), 0.2);
    transition: opacity 0.35s ease-in-out;
}
.overlay.caption.caption-overlay:hover span.bg:after {
    opacity: 1;
}
/*-----------------------------------------------------------------------------------*/
/*	30. TILES
/*-----------------------------------------------------------------------------------*/
.tiles[class*="tiles-"] .items:after {
    content: '';
    display: block;
    clear: both;
}
.tiles[class*="tiles-"] .item {
    float: left;
}
.tiles[class*="tiles-"] .item {
    height: auto
}
.tiles[class*="tiles-"] .item figure {
    position: relative;
    overflow: hidden;
}
.tiles .item:not(.item-detailed) figure img {
    position: relative;
    width: 100%;
    max-width: 100%;
}
.tiles .row .item {
    margin-bottom: 30px;
}
.grid:not(.boxed) .tiles .row .item-detailed {
    margin-bottom: 50px;
}
.tiles.tiles-s .row {
    margin-right: -5px;
    margin-left: -5px;
}
.tiles.tiles-s .row .item {
    padding-right: 5px;
    padding-left: 5px;
    margin-bottom: 10px;
}
.tiles.tiles-m .row {
    margin-right: -10px;
    margin-left: -10px;
}
.tiles.tiles-m .row .item {
    padding-right: 10px;
    padding-left: 10px;
    margin-bottom: 20px;
}
/*-----------------------------------------------------------------------------------*/
/*	31. BLOG
/*-----------------------------------------------------------------------------------*/
.grid-view .post {
    margin-bottom: 3rem;
}
.classic-view .post {
    margin-bottom: 5rem;
}
.meta,
.more,
.breadcrumbs,
.breadcrumbs * {
