/*-----------------------------------------------------------------------------------*/
/*	57. LOADING
/*-----------------------------------------------------------------------------------*/
.page-loading {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #f6f7f8;
    z-index: 9999999;
}
.page-loading .status {
    width: 2rem;
    height: 2rem;
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -1rem 0 0 -1rem;
}
.lg-outer .lg-item:after,
.page-loading .spinner,
.tp-loader.spinner {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    width: 2.5rem;
    height: 2.5rem;
    margin-top: -1.25rem;
    margin-left: -1.25rem;
    background: none;
    border: 4px solid var(--color-white);
    box-shadow: none;
    border-radius: 50%;
    -webkit-animation: loader-scale 1s ease-out infinite;
    animation: loader-scale 1s ease-out infinite;
}
.dark-spinner .tp-loader.spinner {
    border: 4px solid var(--color-dark);
}
@-webkit-keyframes loader-scale {
    0% {
        transform: scale(0);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        transform: scale(1);
        opacity: 0;
    }
}
@keyframes loader-scale {
    0% {
        transform: scale(0);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        transform: scale(1);
        opacity: 0;
    }
}
.page-loading .spinner {
    border-color: #151515;
}
